[_vm._v("El siguiente tema que abarca esta unidad se enfoca en las cadenas de valor o cadenas globales de valor (CGV), también conocidas como cadenas globales de suministro, que, junto con la liberación económica y el cambio tecnológico, han logrado transformar las opciones del crecimiento económico, tanto en países desarrollados como países en vía de desarrollo. Este se puede definir como el rango amplio de actividades que se dirigen a cabo de las firmas en cada uno de sus procesos de producción, que inician desde el diseño, producción, mercadeo, distribución, hasta el servicio al cliente.")]),_c('p',[_vm._v("Las actividades antes mencionadas son realizadas por la misma empresa dentro de una determinada ubicación geográfica o a través de distintas entidades ubicadas de manera diferente. La cadena de valor es principalmente un modo de análisis de las actividades empresariales, por medio del cual se realiza una descomposición de una entidad en cada una de sus partes constitutivas, con el fin de identificar fuentes que permitan lograr una ventaja competitiva en cada área creadora de valor.")])]),_c('div',{staticClass:"col-md-5 col-12",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema3-20.png"),"alt":"alt"}})]),_c('div',{staticClass:"col-12",attrs:{"data-aos":"fade-down"}},[_c('p',[_vm._v("Al pertenecer a las Cadenas Globales de Valor (CGV), los países tienen la posibilidad de obtener una serie de oportunidades con un alto impacto positivo frente a su crecimiento económico, logrando una diversificación de exportaciones, la generación de etapas productivas eficientes, y la adquisición de nuevas capacidades tecnológicas. Para el caso de Colombia, es importante permanecer de una forma activa en una dinámica que promueva el desarrollo económico y el denominado "),_c('i',[_vm._v("Know-how")]),_vm._v(", primordialmente en los sectores con un mayor valor agregado. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4 mt-4"},[_c('div',{staticClass:"col-md-4 col-12",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema3-21.png"),"alt":"alt"}})]),_c('div',{staticClass:"col-md-8 col-12 pr-4",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("Sin embargo, en Colombia se presentan una variedad de factores que evitan una alta inserción en las CGV, como lo son: la concentración en el sector primario que limita el desarrollo de otros sectores; la falta de infraestructura y las barreras arancelarias que protegen eslabones productivos de algunos productos finales que se fabrican en el país. Cabe destacar que el país se encuentra en la apertura de nuevas puertas a los mercados por medio de la suscripción de los acuerdos comerciales con otros países (Quintero y López, 2018). Otro aspecto que ha dificultado la inserción del país en estas cadenas es la subsistencia de múltiples barreras, sean arancelarias o no arancelarias, que apoyan las distintas etapas de la cadena productiva de los bienes finales que se producen dentro del país.")]),_c('p',[_vm._v("Se considera que uno de los sectores que predomina en las cadenas globales de valor es el sector de cuero, calzado y marroquinería, el cual agrupa diversas actividades que inician desde la obtención y tratado del cuero, hasta el diseño y la manufactura de productos de marroquinería y calzado, con el fin de lograr su venta y distribución. Actualmente, en Colombia, se encuentran 737 curtiembres, de las cuales 4 se organizan como gran empresa, 8 como mediana empresa y 75 como pequeña empresa, las cuales se sitúan en municipios tales como: Villapinzón, Chocontá, Cogua y en el sector de San Benito en Bogotá. A pesar de que no se tienen datos precisos ni actualizados, se puede concluir que existe un alto potencial para desarrollar en este sector, el cual debe trabajar en expandir su desarrollo por medio de alianzas estratégicas con el resto del mundo.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-botones mt-4",attrs:{"id":"t_3_4","data-aos":"fade-right"}},[_c('h2',[_vm._v("3.4 Agentes económicos")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4 mt-4"},[_c('div',{staticClass:"col-12 pr-4",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("A continuación, se abordará un tema de alta relevancia, el sistema económico, donde los agentes económicos hacen parte esencial de este enfoque. Los principales actores que forman parte de este sistema son los hogares, las empresas y el gobierno. Observe lo que mencionan al respecto Reina et al. (2006):")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-6 col-lg mb-5 mb-lg-0"},[_c('div',{staticClass:"tarjeta-avatar"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema3-22.svg"),"alt":"AvatarTop"}}),_c('div',{staticClass:"tarjeta color-tarjeta-morado"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"text-center"},[_vm._v("Los hogares son unidades sociales conformadas por las personas que viven en una misma vivienda.")])])])])]),_c('div',{staticClass:"col-md-6 col-lg mb-5 mb-lg-0"},[_c('div',{staticClass:"tarjeta-avatar"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema3-24.svg"),"alt":"AvatarTop"}}),_c('div',{staticClass:"tarjeta color-tarjeta-azul"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"text-center"},[_vm._v("Las empresas son las unidades que llevan a cabo la actividad económica de producción.")])])])])]),_c('div',{staticClass:"col-md-6 col-lg mb-5 mb-lg-0"},[_c('div',{staticClass:"tarjeta-avatar"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema3-23.svg"),"alt":"AvatarTop"}}),_c('div',{staticClass:"tarjeta color-tarjeta-morado"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"text-center"},[_vm._v("El gobierno es el conjunto de instituciones políticas, leyes y prácticas a través de las cuales se realiza la actividad de gobernar.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4 mt-4"},[_c('div',{staticClass:"col-md-5 col-12",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema3-25.png"),"alt":"alt"}})]),_c('div',{staticClass:"col-md-7 col-12 pr-4",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("La principal actividad que realizan las empresas es la producción, mientras que los hogares son los encargados del consumo. De esta manera, son las empresas las que producen los bienes que son consumidos por cada hogar; a su vez, estos mismos son los que adquieren los bienes para satisfacer sus necesidades. El gobierno es considerado un agente especial, si bien no solo se comporta como un agente económico, sino que tiene la función de administrar y regular a todos los individuos que hacen parte del sistema económico. A partir de lo mencionado, el gobierno se debe encargar de proteger las normas que regulan el sistema. ")]),_c('p',[_vm._v("En cuanto a las actividades de consumo, son los hogares quienes participan en la compra de bienes para su consumo, los cuales buscan satisfacer de manera directa sus necesidades. Un ejemplo de los bienes de consumo son los alimentos y la ropa.")])]),_c('div',{staticClass:"col-12 mt-4"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Finalmente, un aspecto de los agentes económicos se puede complementar con una clasificación sobre el tipo de bienes y servicios, los cuales estos mismos son los encargados de intercambiar. Se destaca que las diferencias en el tipo de bienes y servicios que intercambian los diferentes actores económicos