-12 mt-4"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("A partir de lo antes mencionado, se presenta una breve descripción sobre el código CIIU, el cual tiene como fin único clasificar todas las actividades económicas de las entidades del país de una forma exacta. Las cámaras de comercio de Colombia están regidas por la Clasificación Industrial Internacional Uniforme de cada una de las actividades económicas, desde el año 2000. Este código fue desarrollado por la Organización de Naciones Unidas, y la revisión 4 corresponde a una adaptación para el país realizada y supervisada por el Departamento Administrativo Nacional de Estadística (DANE).")]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Su objetivo principal es proporcionar un conjunto de categorías de actividades que pueda utilizarse para la recopilación, codificación y la presentación de informes estadísticos, de acuerdo con esas actividades. En este contexto, el término actividad se entiende como un proceso o grupo de operaciones que combinan recursos tales como equipo, mano de obra, técnicas de fabricación e insumos, para la producción de bienes y servicios. Para lograr dicha división, se tienen en cuenta los siguientes aspectos generales: las características de los bienes producidos y de los servicios prestados; los usos a que se destinan esos bienes y servicios; y los insumos, el proceso y la tecnología de producción. ")]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Las actividades se pueden identificar mediante los siguientes pasos: ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4 mt-4"},[_c('div',{staticClass:"col-12 pr-4"},[_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Por medio de la Resolución 066 del 31 de enero de 2012, el DANE constituye la Clasificación de Actividades Económicas CIIU 4 A.C., siendo una versión oficial, reemplazando de esta manera las anteriores (CIIU 3 A. C. y CIIU 3.1 A.C.) e integrando cambios en la estructura de clasificación. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-2"},[_c('figure',[_c('img',{staticStyle:{"padding":"1.2rem"},attrs:{"src":require("@/assets/curso/temas/website.svg")}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-md-9 col-12"},[_c('h4',[_vm._v("Actividades económicas válidas 2021 - Códigos CIIU ")]),_c('p',[_vm._v("Actividades económicas o códigos CIIU que se encuentran disponibles por parte de la DIAN para el año 2021.")])])}]

